import React from "react"
// import css from 'components/App.module.css'
// import axios from "axios"

import Searchbar from 'components/Searchbar/Searchbar'
import ImageGallery from 'components/ImageGallery/ImageGallery'
import ImageGalleryItem from 'components/ImageGalleryItem/ImageGalleryItem'
import Button from 'components/Button/Button'
import Modal from 'components/Modal/Modal'
import { getAxios } from 'components/Api/GalleryApi'
import Loader from 'components/Loader/Loader'
class App extends React.Component {
  state = {
    items: [],                         //     ,   
    loading: false,                  //c               
    error: null,                       //       
    search: '',                        //            
    page: 1,                          //       
    showModal: false,                 //                
    largeImg: null,                   //  Big picture
    totalPages: 1,
    perPage: 12,
  }

  componentDidUpdate(prevProps, prevState) {               //     
    const { search, page } = this.state                         //     
    if (prevState.search !== search || prevState.page !== page) {   //        prevState.search -          
      this.fetchPosts()                                       //       page ,     this.fetchPosts()
    }
  }                                                       //this.fetchPosts() -          

  async fetchPosts() {
    try {
      this.setState({ loading: true })
      const { search, page ,perPage  } = this.state;
      const {hits,totalHits} = await getAxios(search, page ,perPage )

      this.getTotalPages(totalHits);

      this.setState(({ items }) => ({ items: [...items, ...hits] }));
    }
    catch (error) {
      this.setState({ error: error.message })
    }
    finally {
      this.setState({ loading: false })
    }
  }

  getTotalPages(totalHits) {
    const { perPage } = this.state;
    let pages = Math.floor(totalHits / 12);
    pages = totalHits % perPage ? pages + 1 : pages;
    console.log(pages);
    this.setState({ totalPages: pages });
  }

  searchImage = ({ search }) => {                          //     search       ...
    this.setState({ search, page: 1, items: [] })      //               
  }                                             //              page:1 ,items:[]    

  loadMore = () => {                                          //prevState--    
    this.setState(prevState => ({ page: prevState.page + 1 }))  //    loadmore     +1  page     () 
  }

  showImage = ({ largeImageURL, tags }) => {       //           this.setState     ()=>
    this.setState({
      largeImg: {                //  largeImg        largeImageURL,   tags,
        largeImageURL,
        tags,
      },
      showModal: true,                   //                    howModal:true   
    })
  }

  closeModal = () => {                                         //       
    this.setState({ showModal: false, largeImg: null, })      //          largeImg:null,
  }

  render() {
    const { items, loading, error, showModal, largeImg ,page , totalPages } = this.state;
    const { searchImage, loadMore, showImage, closeModal } = this;
    console.log(this.state)
  
    return (
      <>
        <Searchbar onSubmit={searchImage} />
        <ImageGallery><ImageGalleryItem items={items} showImage={showImage} /></ImageGallery>

        {loading && <Loader />}
        {error && <p>Something go wrong</p>}
        {Boolean(items.length) && <Button onClick={loadMore} page={page} totalPages={totalPages} />}
        {showModal && <Modal largeImg={largeImg} close={closeModal} />}

      </>
    )
  }
}
export default App




